import {useEffect, useState} from "react"

import { Link } from "react-router-dom"

import { Container, Row, Col } from "reactstrap"

import { db } from "@meddbriefer/mdb-firebase"

import MDBNavBar from "../components/navbar/MDBNavBar"

const ScenarioList = (props) => {
    const [scenarios, setScenarios] = useState([])
    const [orderRequired, setOrderRequired] = useState(false)

    useEffect(
        () => {
            db.collection("class-codes").doc("demo").get()
            .then(doc => {
                const tmp = doc.data()
                setOrderRequired(tmp.orderRequired)
                let assignments = tmp.assignments
                if (tmp.orderRequired) {
                    assignments.sort((a, b) => a.order - b.order)
                }
                setScenarios(assignments)
            })
            .catch(error => console.log(error))
        }, []
    )
    return (
        <>
            <MDBNavBar title="Scenario List" />
            <Container>
                <Row>
                    <Col sm="3"></Col>
                    <Col sm="6">
                        <h2>Your Assigned Scenarios:</h2>
                        <ul>
                            {scenarios.map((scen) => (
                                <li key={scen.id}>
                                    <Link className="h3" to={`/scenario/${scen.id}`}> {scen.name}</Link>
                                </li>
                            ))}
                        </ul>
                    </Col>
                    <Col sm="3"></Col>
                </Row>
            </Container>
        </>
    )
}

export default ScenarioList